}
.text {
  text-align: center;
}

/* WP Button */
.popout {
  position: fixed;
  bottom: 2.5rem;
  right: 2.5rem;
}

.popout .btn {
  display: inline-block;
  position: relative;
  outline: none;
  -webkit-user-select: none;
  user-select: none;
  cursor: pointer;
  width: 50px;
  height: 50px;
  line-height: 60px;
  text-align: center;
  font-size: 32px;
  z-index: 999;
  background: #eee;
  color: #fff;
  border-radius: 50%;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.15);
  transition: 0.2s opacity ease-in-out;
  -webkit-transition: 0.2s opacity ease-in-out;
}
.popout .btn .wp-icon {
  display: inline-flex;
}
.popout .btn.active {
  visibility: hidden;
  opacity: 0;
}

.popout .btn:after {
  display: block;
  position: absolute;
  top: 0;
  content: '';
  border-radius: 50%;
  width: 50px;
  height: 50px;
  background: #10f568;
  z-index: -2;
  -webkit-transition: -webkit-transform 0.2s, opacity 0.2s;
  -moz-transition: -moz-transform 0.2s, opacity 0.2s;
  transition: transform 0.2s, opacity 0.2s;
}

.popout .btn:active:after {
  -webkit-transform: scale(1.3);
  -moz-transform: scale(1.3);
  -ms-transform: scale(1.3);
  transform: scale(1.3);
  opacity: 0;
}
